import ListFlexItem from '@/components/list_flex_item.vue';
import EditTime from '@/components/edit_time.vue';
import EditSelect2 from '@/components/edit_select2.vue';

export default {
  components: {
    ListFlexItem,
    EditTime,
    EditSelect2
  },
  data () {
    return {
      inputValue: '',
      isInputFocused: false,
      isResultShow: false,
      isFilterShow: false,
      filterParams: {
        period: '',
        StartTime: '',
        EndTime: '',
        operationType: '',
        workType: '',
        planStatus: '',
        railwayBureau: ''
      },
      cellsData: [
        {
          firstLeft: '01月12日作业',
          firstRight: '信阳东站',
          secondLeft: '09:32:12-13:32:12 4:12:21',
          isFinish: false,
          id: 1
        },
        {
          firstLeft: '01月12日作业',
          firstRight: '信阳东站',
          secondLeft: '09:32:12-13:32:12  4:12:21',
          isFinish: true,
          id: 2
        },
        {
          firstLeft: '01月12日作业',
          firstRight: '信阳东站',
          secondLeft: '09:32:12-13:32:12  4:12:21',
          isFinish: true,
          id: 3
        }
      ],
      inputParams1: [
        {
          title: '开始日期',
          default_time: '请选择打卡开始日期'
        },
        {
          title: '结束日期',
          default_time: '请选择打卡结束日期'
        }
      ],
      inputParams2: [
        {
          title: '工作路局',
          list: [['北京铁路局', '上海铁路局', '山西铁路局']],
          val: [],
          placeholder: '请选择工作路局'
        },
        {
          title: '打卡状态',
          list: [['未完成', '已完成']],
          val: [],
          placeholder: '请选择打卡状态'
        }
      ],
    }
  },
  methods: {
    focusInput () {
      let input = document.getElementById('search_input')
      input.focus()
      this.isInputFocused = true
    },
    blurInput () {
      if (!this.inputValue.trim()) this.cancelSearch()
    },
    onInput () {
      this.isResultShow = this.inputValue.trim() ? true : false
    },
    cancelSearch () {
      this.isInputFocused = false
      this.isResultShow = false
      this.inputValue = ''
    },
    clearSearch () {
      this.isResultShow = false
      this.inputValue = '' 
    },
    resetForm () {
      this.inputParams2.forEach((item, index) => {
        item.val = []
      })
      this.inputParams1[0].default_time = '请选择打卡开始日期'
      this.inputParams1[1].default_time = '请选择打卡结束日期'
      this.filterParams.period = ''
    }
  }
}



